import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/project-img1.png";
import tempImg1 from "../assets/img/placeholder-img1.png";
import tempImg2 from "../assets/img/placeholder-img2.png";
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Projects = () => {

  const projects = [
    {
      title: "Personal Portfolio",
      description: "Built with React, JavaScript, HTML, CSS, Bootstrap. The Purpose of this project is to share personal projects, showcasing a part of my ability to program.",
      imgUrl: projImg1,
    },
    {
      title: "Blank",
      description: "Blank",
      imgUrl: tempImg1,
    },
    {
      title: "Blank",
      description: "Blank",
      imgUrl: tempImg2,
    },
    {
      title: "Blank",
      description: "Blank",
      imgUrl: tempImg1,
    },
    {
      title: "Blank",
      description: "Blank",
      imgUrl: tempImg2,
    },
    {
      title: "Blank",
      description: "Blank",
      imgUrl: tempImg1,
    },
  ];

  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                <h2>Projects</h2>
                <p>Projects that I've finished. May add old school projects later.</p>
                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                  <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                    <Nav.Item>
                      <Nav.Link eventKey="first">Tab 1</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="second">Tab 2</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="third">Tab 3</Nav.Link>
                    </Nav.Item>
                  </Nav>
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first">
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="second">
                      <p>Second tab.</p>
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                      <p>Third tab.</p>
                    </Tab.Pane>
                  </Tab.Content>
                </Tab.Container>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
